import React, { useState, useEffect } from 'react';
import axios from 'axios';

function ColumnSelector({ databaseId, selectedTable, onColumnSelect }) {
  const [columns, setColumns] = useState([]);
  const [selectedColumn, setSelectedColumn] = useState('');

  useEffect(() => {
    if (databaseId && selectedTable) {
      // Updated API call for the namespaced endpoint
      axios.get(`/api/databases/${databaseId}/tables/${selectedTable}/columns`)
        .then(response => {
          // Ensure response.data is in the expected format, an array
          const columnsData = Array.isArray(response.data) ? response.data : [];
          setColumns(columnsData);
        })
        .catch(error => {
          console.error("Fetching columns failed", error);
          setColumns([]); // Ensure state is reset to an empty array on error
        });
    } else {
      setColumns([]); // Reset columns if the database or table selection is cleared
    }
  }, [databaseId, selectedTable]);

  const handleColumnChange = (e) => {
    const column = e.target.value;
    setSelectedColumn(column);
    if (onColumnSelect) {
      onColumnSelect(column);
    }
  };

  console.log("Columns before map:", columns); // Detailed log
if (!Array.isArray(columns)) {
  console.error("Columns is not an array:", columns);
}
  return (
    <div>
      <label>Select a Column: </label>
      <select value={selectedColumn} onChange={handleColumnChange}>
        <option value="">--Select--</option>
        {columns.map((column, index) => (
          <option key={index} value={column}>{column}</option>
        ))}
      </select>
    </div>
  );
}

export default ColumnSelector;
