import React, { useState, useEffect } from 'react';
import axios from 'axios';
import './App.css';
import TabbedInterface from './components/TabbedInterface';

function App() {
    const [selectedTable, setSelectedTable] = useState('');
    const [webSocket, setWebSocket] = useState(null);
    const [selectedDatabase, setSelectedDatabase] = useState('');
    const [databases, setDatabases] = useState([]);
    const [tables, setTables] = useState([]);
    const [columns, setColumns] = useState([]);
    const [error, setError] = useState('');

    useEffect(() => {
        // Initialize WebSocket connection and fetch default database name
        const ws = new WebSocket('ws://3.252.131.54:8002/ws/live-updates/');
        setWebSocket(ws);

        ws.onopen = () => console.log('WebSocket Connected');
        ws.onmessage = (e) => handleWebSocketMessage(JSON.parse(e.data));
        ws.onclose = () => console.log('WebSocket Disconnected');
        ws.onerror = (error) => setError(`WebSocket encountered an error: ${error.message}`);

        fetchDefaultDatabase();

        // Fetch tables for the initially selected database
        // Note: This assumes fetchTableNames is correctly defined and will replace fetchTables
        fetchTableNames();

        return () => ws.close();
    }, []); // Empty dependency array means this effect runs once on mount

    const handleWebSocketMessage = (message) => {
        // Implementation remains the same
    };

    const fetchDefaultDatabase = async () => {
        // Implementation remains the same
    };

    const fetchTableNames = async () => {
        if (selectedDatabase) {
            try {
                const response = await axios.get(`/api/fetch-tables/${selectedDatabase}/`);
                setTables(response.data.tables); // Assuming the API returns { tables: [...] }
            } catch (error) {
                console.error("Fetching table names failed: ", error);
                setError(`Failed to fetch table names: ${error.message}`);
            }
        }
    };

    const fetchColumns = async () => {
        // Implementation remains the same
    };

    return (
        <div className="App">
            <h1>Database Interface</h1>
            {error && <p className="error">{error}</p>}
            <TabbedInterface
                databases={databases}
                selectedDatabase={selectedDatabase}
                selectedTable={selectedTable}
                onDatabaseChange={(dbName) => {
                    setSelectedDatabase(dbName);
                    fetchTableNames(); // Assuming this function now correctly fetches tables for the selected database
                    setColumns([]);
                }}
                tables={tables}
                columns={columns}
                onTableChange={setSelectedTable}
                onColumnSelect={() => {}} 
            />
        </div>
    );
}

export default App;
