import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { useSelector } from 'react-redux'; 

function TableSelector({ onTableSelect }) { 
    const [tables, setTables] = useState([]);
    const [selectedTable, setSelectedTable] = useState('');

    // Fetch selectedDatabase from Redux store
    const selectedDatabase = useSelector(state => state.promptBuilder.selectedDatabase); // Adjust reducer name if needed

    useEffect(() => {
        if (selectedDatabase) { 
            // Updated API call for the namespaced endpoint
            axios.get(`/api/databases/${selectedDatabase}/`) 
                .then(response => {
                    setTables(response.data.tables || []);
                })
                .catch(error => {
                    console.error("Fetching tables failed", error);
                    setTables([]);
                });
        } else {
            setTables([]); // Clear tables if no database is selected
        }
    }, [selectedDatabase]); 

    const handleTableChange = (e) => {
        const table = e.target.value;
        setSelectedTable(table);
        if(onTableSelect) {
            onTableSelect(table);
        }
    };

    return (
        <div>
            <label>Select a Table: </label>
            <select value={selectedTable} onChange={handleTableChange}>
                <option value="">--Select--</option>
                {tables.map((table, index) => (
                    <option key={index} value={table}>{table}</option>
                ))}
            </select>
        </div>
    );
}

export default TableSelector;
