import React from 'react';
import ReactDOM from 'react-dom/client'; // Use this import for React 18
import './index.css';
import App from './App';
import reportWebVitals from './reportWebVitals';
import { Provider } from 'react-redux';
import store from './redux/store'; // Adjust the import path based on your project structure
import axios from 'axios';

// Correct way to use ReactDOM.createRoot with Redux Provider in React 18
const root = ReactDOM.createRoot(document.getElementById('root'));
root.render(
  <React.StrictMode>
    <Provider store={store}>
      <App />
    </Provider>
  </React.StrictMode>
);

// Set the Axios default base URL
axios.defaults.baseURL = 'http://3.252.131.54:8000/';

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
