// src/redux/promptBuilderSlice.js
import { createSlice } from '@reduxjs/toolkit';

export const promptBuilderSlice = createSlice({
  name: 'promptBuilder',
  initialState: {
    templates: [],
    datasets: [],
    items: [], // Ensure you have this in your initialState
    selectedTemplate: null,
    selectedDataSet: null,
    processedText: '',
    loading: false,
    error: null,
  },
  reducers: {
    setTemplates: (state, action) => {
      state.templates = action.payload;
    },
    setDatasets: (state, action) => {
      state.datasets = action.payload;
    },
    setItems: (state, action) => {
      state.items = action.payload; // Add this reducer for setting items
    },
    setSelectedTemplate: (state, action) => {
      state.selectedTemplate = action.payload;
    },
    setSelectedDataSet: (state, action) => {
      state.selectedDataSet = action.payload;
    },
    setProcessedText: (state, action) => {
      state.processedText = action.payload;
    },
    setLoading: (state, action) => {
      state.loading = action.payload;
    },
    setError: (state, action) => {
      state.error = action.payload;
    },
  },
});

// Export all action creators
export const {
  setTemplates,
  setDatasets,
  setItems, // Make sure to export this
  setSelectedTemplate,
  setSelectedDataSet,
  setProcessedText,
  setLoading,
  setError
} = promptBuilderSlice.actions;

export default promptBuilderSlice.reducer;
